/* Styling for the introduction section on the homepage */
.intro_wrapper {
  color: #fff;
  margin: 1% 0;
  height: 100px;
  width: 100%; }

.imageandtext {
  margin-left: 20px;
  top: 3.5%;
  position: absolute; }

.logo {
  -webkit-box-shadow: 0 3px 25px 0 rgba(0, 0, 0, 0.16);
          box-shadow: 0 3px 25px 0 rgba(0, 0, 0, 0.16);
  border-radius: 50%;
  margin-left: 10px; }

.beta {
  color: #000;
  display: inline-block;
  font-family: "AvenirNext-Regular", Helvetica;
  font-size: 20px;
  font-style: normal;
  font-stretch: normal;
  font-weight: normal;
  letter-spacing: 1px;
  vertical-align: bottom;
  margin-left: -10px; }
  .beta.mobile {
    display: none; }

.heading {
  display: inline-block;
  font-family: "AvenirNext-Regular", Helvetica;
  font-size: 48px;
  font-weight: 700;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.37;
  letter-spacing: 2px;
  color: #000;
  margin-left: 20px;
  vertical-align: middle;
  margin-bottom: -2px; }

.buttons {
  float: right;
  display: inline;
  margin-top: 30px;
  margin-right: 50px; }
  .buttons a {
    margin: 0 3vw;
    font-size: 14px;
    font-weight: 400;
    line-height: 1.38;
    letter-spacing: .8px;
    text-align: left;
    color: #080808;
    border: none;
    padding-bottom: 3.5px; }
    .buttons a.login {
      color: white;
      border-radius: 5px;
      background-color: #fbbc12;
      -webkit-box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.16);
              box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.16);
      font-size: 19px;
      padding: 10px;
      font-weight: bold; }
      .buttons a.login:hover {
        text-decoration: underline;
        border-bottom: none; }
    .buttons a:hover {
      text-decoration: none;
      border-bottom: 1px solid black; }

.tagline {
  color: black;
  margin-left: 20px;
  display: inline-block;
  margin-left: 20px;
  font-size: 15px; }

.text_wrapper {
  display: inline-block;
  top: 20px;
  position: relative; }

@media only screen and (max-width: 990px) {
  .buttons a {
    visibility: hidden; } }

@media only screen and (max-width: 439px) {
  p.heading {
    margin-top: 0px;
    font-size: 33px; }
  .beta {
    font-size: 10px; }
  .tagline {
    font-size: 11px; } }

@media only screen and (max-width: 420px) {
  #intro .logo {
    width: 75px;
    height: 75px; }
  #intro .text_wrapper {
    top: 12px; }
  .introLogin_wrapper {
    display: none; }
  h4 {
    padding-bottom: 5vh; } }



