body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  overflow-x: hidden;
  overflow-y: auto; }
  body img {
    max-width: 100%; }
  body .canvas-wrapper {
    /* overwriting rc-slider css*/ }
    body .canvas-wrapper #filter_bar {
      min-width: 1100px; }
      body .canvas-wrapper #filter_bar .rc-slider {
        width: 96.5%; }
      body .canvas-wrapper #filter_bar .rc-slider-handle {
        border: 1px solid white;
        background-color: #fbc305;
        -webkit-box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.16);
                box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.16); }
      body .canvas-wrapper #filter_bar .rc-slider-dot-active {
        border-color: #95989a; }
      body .canvas-wrapper #filter_bar .rc-slider-track {
        background-color: #172650; }
      body .canvas-wrapper #filter_bar .rc-slider-mark-text-active {
        color: #767676; }

a {
  cursor: pointer; }

@media (min-width: 768px) {
  .canvas-wrapper .navbar-right, #home-wrapper .navbar-right {
    margin-right: 26px; } }



