.toast-box {
  position: fixed;
  opacity: 0;
  -webkit-transition: 0.25s;
  -o-transition: 0.25s;
  transition: 0.25s;
  bottom: 40px;
  left: 0;
  right: 0;
  text-align: center;
}
.toast-box.show {
  z-index: 200;
  opacity: 1;
  -webkit-transform: translateY(-40px);
      -ms-transform: translateY(-40px);
          transform: translateY(-40px);
}
.toast-box h4 {
  -webkit-box-shadow: 1px 1px 10px #999;
          box-shadow: 1px 1px 10px #999;
  display: inline-block;
  padding: 10px 20px;
  border-radius: 50px;
  margin: 0;
  background: white;
}


