.notification-box {
  position: absolute;
  right: -265px;
  background: white;
  -webkit-box-shadow: 1px 1px 10px #999;
          box-shadow: 1px 1px 10px #999;
  padding: 20px;
  margin: 20px;
  opacity: 0;
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
}
.notification-box.show {
  opacity: 1;
  -webkit-animation: box-show 3s;
          animation: box-show 3s;
  -webkit-transform: translateX(-305px) translateX(40px);
      -ms-transform: translateX(-305px) translateX(40px);
          transform: translateX(-305px) translateX(40px);
}
.notification-box h2 {
  margin: 0;
}


