/** Styling for mobile app informational section on the homepage **/
.mobileApp_wrapper {
  background-color: #151515;
  text-align: center; }

.mobileApp_content_wrapper {
  padding: 0% 4.5% 4%;
  color: white;
  display: inline-block;
  vertical-align: middle;
  width: 48%;
  text-align: left; }
  .mobileApp_content_wrapper p {
    font-weight: lighter;
    font-size: 17px;
    letter-spacing: 0.5px;
    margin-bottom: 4vh;
    line-height: 1.8; }

#homeBadge {
  margin-right: 5px; }

.mobileApp_content_wrapper .ios-headline {
  font-size: 24px;
  font-weight: bold;
  font-style: normal;
  font-stretch: normal;
  line-height: 1.17;
  margin-bottom: 2vh;
  letter-spacing: normal;
  color: #f9bb12;
  font-family: "AvenirNext-Regular", Helvetica; }

.mobileApp_content_wrapper h2.headline {
  margin-bottom: 3vh; }

.mobileApp_image_wrapper {
  display: inline-block;
  max-width: 100%;
  width: 51%; }

#appleBadge {
  max-width: 100%; }

#mobile_top {
  display: inline-block; }

#mobile_bottom {
  display: none; }

@media only screen and (max-width: 800px) {
  #mobile {
    padding: 5vh 0; }
    #mobile #mobile_top {
      display: none; }
    #mobile #mobile_bottom {
      display: inline-block;
      width: 100%;
      padding: 0 5vw; }
      #mobile #mobile_bottom img {
        width: 100%; }
      #mobile #mobile_bottom #appleBadge {
        width: 44%; }
      #mobile #mobile_bottom #googleBadge {
        width: 51%; } }

@media only screen and (max-width: 420px) {
  .mobileApp_image_wrapper.image_wrapper {
    width: 100%; } }



